         <span>{{item.pieceName}}</span><span>({{item.counts}})</span>
         <span style="float: right">{{item.time}}</span>
       </li>
     </ul>
   </div>
  </div>
</template>

<script>
export default {
  name: 'pending',
  data () {
    return {
    }
  },
  props:['listData','itemShow'],
  methods:{

  },
  created(){

  },
  mounted(){

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .pending_header{
     border:1px solid #ddd;
     height: 40px;
     line-height: 40px;
     box-sizing: border-box;
     padding:0 5px;
    color: #303133;
    font-size: 13px;
    font-weight: 500;
  }
  li{
    height: 40px;
    line-height: 40px;
    color: #303133;
    font-size: 13px;
    font-weight: 500;
  }

</style>



