<template>
  <div class="warning">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane  :name="tabName.name" v-for="tabName in tabNames" :label="tabName.label" :key="tabName.index"></el-tab-pane>
    </el-tabs>
    <keep-alive>
           <component :is="currentView" :list-data="itemData" :item-show="itemShow"></component>
    </keep-alive>
  </div>
</template>

<script>
  import Pending from './Pending.vue'
  import Finished from './Finished.vue'
  import Processing from './Processing.vue'
  import Timeout from './Timeout.vue'

export default {
  name: 'warning',
  components:{
    Pending,
    Finished,
    Processing,
    Timeout
  },
  data () {
    return {
      tabNames:[
        {label:'待处理',name:'Pending',data:{}},
        {label:'已完成',name:'Finished',data:{}},
        {label:'处理中',name:'Processing',data:{}},
        {label:'超时单',name:'Timeout',data:{}}
      ],
      itemData:{},
      itemShow:false,
      activeName: 'Pending',
      currentView: 'Pending'
    }
  },
  methods:{
    handleClick(tab, event) {
      console.log(tab, event);
      this.currentView = tab.name;
      this.itemData = this.tabNames[tab.index].data;
      this.itemShow = JSON.stringify(this.itemData) == "{}"? false:true;
      console.log(this.itemShow)
    },
  },
  created(){
    var _this = this;
    this.$http.get('../mobile/taskWorkbar/listTaskWork.htm').then(function (res) {
      var res = res.data.res;
      console.log('请求成功')
      // console.log(res);
      // console.log(JSON.parse(res));
      // console.log(JSON.parse(JSON.parse(res).taskAlertInfoList));
      _this.itemData = JSON.parse(JSON.parse(res).taskAlertInfoList).GX0000142 ? JSON.parse(JSON.parse(res).taskAlertInfoList).GX0000142:{};
      _this.itemShow = JSON.stringify(_this.itemData) == "{}"? false:true;
      _this.tabNames[0].data = JSON.parse(JSON.parse(res).taskAlertInfoList).GX0000142 ?JSON.parse(JSON.parse(res).taskAlertInfoList).GX0000142:{};
      _this.tabNames[1].data = JSON.parse(JSON.parse(res).taskAllocatedList).GX0000142 ?JSON.parse(JSON.parse(res).taskAllocatedList).GX0000142:{};
      _this.tabNames[2].data = JSON.parse(JSON.parse(res).taskCompleteList).GX0000142 ?JSON.parse(JSON.parse(res).taskCompleteList).GX0000142 :{};
      _this.tabNames[3].data = JSON.parse(JSON.parse(res).taskExecutionList).GX0000142 ? JSON.parse(JSON.parse(res).taskExecutionList).GX0000142:{};
    }).catch(function (err) {
      console.log('请求失败')
    })
  },
  mounted(){

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



