import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import TabsBar from '@/components/TabsBar'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'TabsBar',
      component: TabsBar
    }
  ]
})



